$(document).ready(function(){
	var $sitemapBtn = $( '.btn_sitemap' ),
		$serviceRegBtn = $( '.service_reg_btn' ),
		$serviceAdmBtn = $( '.service_adm' ),
		$prayerPreviewBtn = $( '.prayerPreviewBtn' ),
		$prayerPreviewBigBtn = $( '.prayerPreviewBigBtn' ),
		$sitemapCloseBtn = $( '.btn_sitemap_colse' ),
		$serviceRegCloseBtn = $( '.btn_service_colse' ),
		$serviceRegCloseBtn2 = $( '.btn_service_area .btn_cancel' ),
		$serviceAdmCloseBtn = $( '#service_adm_pop .btn_close' ),
		$prayerAdmCloseBtn = $( '#prayer_pop .btn_close' ),
		$prayerAdmBigCloseBtn = $( '#prayer_big_pop .btn_close' ),
		$header = $( '#main' ),
		$header2 = $( '#service_reg_pop' ),
		$header3 = $( '#service_adm_pop' ),
		$header4 = $( '#prayer_pop' ),
		$header5 = $( '#prayer_big_pop' ),
		$dimmed = $( '#deemed_main' );
	
	var isMenu = false;

	// sitemap�޴�
	$sitemapBtn.on( 'click', function (e) {
		$header.addClass( 'active' );
		$dimmed.addClass( 'active' );
		return false;
	});

	$sitemapCloseBtn.on( 'click', function (e) {
		$header.removeClass( 'active' );
		$dimmed.removeClass( 'active' );
		return false;
	});

	$serviceRegBtn.on( 'click', function (e) {
		$header2.addClass( 'active' );
		$dimmed.addClass( 'active' );
		return false;
	});

	$serviceRegCloseBtn.on( 'click', function (e) {
		$header2.removeClass( 'active' );
		$dimmed.removeClass( 'active' );
		return false;
	});

	$serviceRegCloseBtn2.on( 'click', function (e) {
		$header2.removeClass( 'active' );
		$dimmed.removeClass( 'active' );
		return false;
	});

	$serviceAdmBtn.on( 'click', function (e) {
		$header3.addClass( 'active' );
		$dimmed.addClass( 'active' );
		return false;
	});

	$prayerPreviewBtn.on( 'click', function (e) {
		$header4.addClass( 'active' );
		$dimmed.addClass( 'active' );
		return false;
	});

	$prayerPreviewBigBtn.on( 'click', function (e) {
		$header5.addClass( 'active' );
		$dimmed.addClass( 'active' );
		return false;
	});

	$serviceAdmCloseBtn.on( 'click', function (e) {
		$header3.removeClass( 'active' );
		$dimmed.removeClass( 'active' );
		return false;
	});

	$prayerAdmCloseBtn.on( 'click', function (e) {
		$header4.removeClass( 'active' );
		$dimmed.removeClass( 'active' );
		return false;
	});
	$prayerAdmBigCloseBtn.on( 'click', function (e) {
		$header5.removeClass( 'active' );
		$dimmed.removeClass( 'active' );
		return false;
	});

	$('.bugo_tab li').click(function(){
		var tabType = $(this).index();
		$('.bugo_tab_contents').eq(tabType).show().siblings('.bugo_tab_contents').hide();
		$('.bugo_tab li').each(function(index){
			$(this).addClass( 'active' );
			if(tabType != index) {
				$(this).removeClass( 'active' );
			}
		});
	});

	$('.service_tab li').click(function(){
		var tabType = $(this).index();
		$('.service_contents').eq(tabType).show().siblings('.service_contents').hide();
		$('.service_tab li').each(function(index){
			$(this).addClass( 'active' );
			if(tabType != index) {
				$(this).removeClass( 'active' );
			}
		});
	});

	$('.messageReg').click(function(){
		$('.messageRegBox').show();
		$('.messageReg').hide();	
	});

	$('.btn_close_wrap').on( 'click', function (e) {
		$( '#deemed').addClass( 'active' );
		$( '.close_wrap').addClass( 'active' );
		return false;
	});
  // faq Ŭ���� ����
	var $subTopMenu = $( '.faq_list .q_btn' );
	$subTopMenu.on( 'click', function () {
		var index = $( this ).index();
		if ( $( this ).parent().hasClass( 'active' )) {
			$( this ).parent().removeClass( 'active' );
		} else {
			$( this ).parent().addClass( 'active' );
		}
		return false;
	});

	 // ����/����
	$('.choiceArea_inner li.religion').click(function(){
		var tabType = $(this).index();
		$('.catArea').eq(tabType).show().siblings('.catArea').hide();
		$('.choiceArea_inner li.religion button').each(function(index){
			$(this).addClass( 'active' );
			if(tabType != index) {
				$(this).removeClass( 'active' );
			}
		});

		if(tabType == 0) {
			$('.leftArea04').addClass( 'active' );
		} else {
			$('.leftArea04').removeClass( 'active' );
		}

		if(tabType > 0) {
			$('.leftArea05').addClass( 'active' );
			$('.instructor_title_rightArea').addClass( 'active' );
		} else {
			$('.leftArea05').removeClass( 'active' );
			$('.instructor_title_rightArea').removeClass( 'active' );
		}

		for(i=1; i < 4; i++) {
			if(tabType == i) {
				$('.patternView'+tabType).addClass( 'active' );
			} else {
				$('.patternView'+i).removeClass( 'active' );
			}
		}
	});
});

